interface IStringTMap<T> { [key: string]: T }
interface IStringStringMap extends IStringTMap<string> { }

interface IOperation {
  key: string,
  values: IStringStringMap[]
}

type ISelection = d3.Selection<d3.BaseType, {}, Element, {}>

(function() {

  const locale = d3.formatLocale({
    decimal: ',',
    thousands: '.',
    grouping: [3],
    currency: ['€', '']
  });

  const format = locale.format(',');

  const viz = d3.select('#viz');

  // Buttons
  const buttons = viz.append('div')
    .attr('id', 'buttons');

  buttons.append('button')
    .attr('id', 'abs-button')
    .attr('class', 'w3-button w3-margin active')
    .text('Totaal aantal')
    .on('click', toggleData);

  buttons.append('button')
    .attr('id', 'rel-button')
    .attr('class', 'w3-button w3-margin')
    .text('Per 10.000 inwoners')
    .on('click', toggleData);

  function toggleData() {

    let thisButton = d3.select(this);
    let otherButton = this.id === 'abs-button' ? d3.select('#rel-button') : d3.select('#abs-button');

    if (!thisButton.classed('active')) {
      thisButton.classed('active', true);
      otherButton.classed('active', false);
      if (this.id === 'abs-button') {
        d3.selectAll('.abs').style('visibility', 'visible');
        d3.selectAll('.rel').style('visibility', 'hidden');
      } else {
        d3.selectAll('.abs').style('visibility', 'hidden');
        d3.selectAll('.rel').style('visibility', 'visible');
      }
    }
  }

  // Legend
  const legend = viz.append('div')
    .attr('id', 'legend')
    .append('svg')
    .attr('viewBox', '0 0 320 120');

  // colors from w3 flat ui colors
  const palette = [
    { description: 'Dagopname', color: '#f1c40f' }, // sunflower
    { description: 'Klinisch', color: '#c0392b' }, // pomegranate
    { description: 'Dagopname, specifiek (laparoscopisch of secundair)', color: '#f39c12' }, // orange
    { description: 'Klinisch, specifiek (laparoscopisch of secundair)', color: '#e74c3c' } // alizarin
  ];

  function addLegend(x: number, y: number, paletteItem: IStringStringMap) {
    legend.append('rect')
      .attr('x', x)
      .attr('y', y)
      .attr('width', 17)
      .attr('height', 17)
      .style('fill', paletteItem.color);

    legend.append('text')
      .attr('x', x + 20)
      .attr('y', y + 12)
      .style('font-size', '1.2em')
      .text(paletteItem.description);
  }

  addLegend(4, 20, palette[0]);
  addLegend(4, 45, palette[1]);
  addLegend(4, 70, palette[2]);
  addLegend(4, 95, palette[3]);

  // Chart-containers
  const bigTotalChartContainer = viz.append('div')
    .attr('id', 'big-total-chart-container');
  const chartContainer = viz.append('div')
    .attr('id', 'chart-container');

  // Big chart with the total
  const marginTotal = { top: 10, right: 20, bottom: 20, left: 66 };
  const widthTotal = 650 - marginTotal.right - marginTotal.left;
  const heightTotal = 150 - marginTotal.top - marginTotal.bottom;

  const xTotal = d3.scaleTime().domain([new Date(1995, 0, 1), new Date(2010, 0, 1)]).range([0, widthTotal]);
  const xAxisTotal = d3.axisBottom(xTotal).tickSizeInner(6).tickSizeOuter(0);
  const yTotal = d3.scaleLinear().range([heightTotal, 0]);
  const yTotalAxis = d3.axisLeft(yTotal).ticks(4).tickFormat(format);

  // Regular charts 
  const margin = { top: 10, right: 20, bottom: 20, left: 60 };
  const width = 310 - margin.right - margin.left;
  const height = 100 - margin.top - margin.bottom;

  const x = d3.scaleTime().domain([new Date(1995, 0, 1), new Date(2010, 0, 1)]).range([0, width]);
  const xAxis = d3.axisBottom(x).tickSizeInner(6).tickSizeOuter(0);
  const y = d3.scaleLinear().range([height, 0]);
  const yAxis = d3.axisLeft(y).ticks(3).tickSizeOuter(0).tickFormat(format);

  // Chart 'Staaroperatie'
  const heightLarge = 3 * height;
  const yLarge = d3.scaleLinear().range([heightLarge, 0]);
  const yLargeAxis = d3.axisLeft(yLarge).ticks(3).tickSizeOuter(0).tickFormat(format);

  d3.csv('operaties-1995-2010.csv', (err, csv) => {

    const dataRaw: IOperation[] = d3.nest()
      .key((d: IStringStringMap) => d.operatie)
      .entries(csv);
    dataRaw.unshift({ key: dataRaw[0].key, values: dataRaw[0].values }); // Add copy of the first data object 'Alle operatieve verrichtingen' for big total chart
    dataRaw[0].key = 'Alle operatieve verrichtingen BIG TOTAL';

    const years = d3.keys(csv[0]).slice(0, 16); //this slicing cuts off the elements 'aantal_of_per10000', 'operatie' and 'wijze'. So we only keep the year-elements

    const dataAbsolute = dataRaw.map(operation => ({ key: operation.key, values: operation.values.filter(d => d.aantal_of_per10000 === 'klinisch aantal' || d.aantal_of_per10000 === 'dagopname aantal') }));
    const dataRelative = dataRaw.map(operation => ({ key: operation.key, values: operation.values.filter(d => d.aantal_of_per10000 === 'klinisch per 10000 inw' || d.aantal_of_per10000 === 'dagopname per 10000 inw') }));

    const dataAbsoluteByYear: { key: string, values: {}[] }[] = dataAbsolute.map(d => operationWithValuesByYear(d, years));
    const dataRelativeByYear: { key: string, values: {}[] }[] = dataRelative.map(d => operationWithValuesByYear(d, years));

    const chartsAbs = dataAbsoluteByYear;
    const chartsRel = dataRelativeByYear;

    function chart(data: { key: string, values: { [key: string]: number | string }[] }, absOrRel: string) {

      let yMax;
      let chartHeight;
      let chartX = x;
      let chartXAxis = xAxis;
      let chartY: d3.ScaleLinear<number, number>;
      let chartYAxis;

      switch (data.key) {
        case 'Alle operatieve verrichtingen BIG TOTAL':
          yMax = absOrRel === 'abs' ? 1500000 : 1000;
          yTotal.domain([0, yMax]);
          chartHeight = heightTotal;
          chartX = xTotal;
          chartXAxis = xAxisTotal;
          chartY = yTotal;
          chartYAxis = yTotalAxis;
          break;
        case 'Alle operatieve verrichtingen':
          yMax = absOrRel === 'abs' ? 1500000 : 1000;
          y.domain([0, yMax]);
          chartHeight = height;
          chartY = y;
          chartYAxis = yAxis;
          break;
        case 'Staaroperatie':
          yMax = absOrRel === 'abs' ? 150000 : 90;
          yLarge.domain([0, yMax]);
          chartHeight = heightLarge;
          chartY = yLarge;
          chartYAxis = yLargeAxis;
          break;
        default:
          yMax = absOrRel === 'abs' ? 50000 : 30;
          y.domain([0, yMax]);
          chartHeight = height;
          chartY = y;
          chartYAxis = yAxis;
          break;
      }

      const keysForStack = d3.keys(data.values[0]).filter(key => key !== 'year');
      const stack = d3.stack()
        .keys(keysForStack);

      const area = d3.area()
        .x(d => chartX(new Date(+(<any>d).data.year, 0, 0)))
        .y1(d => chartY(d[1]))
        .y0(d => chartY(d[0]));

      const svg = d3.select('#' + data.key.toLowerCase().replace(/[\s/,\.]/g, '')).select('svg').select('g');

      svg.append('g')
        .attr('class', absOrRel)
        .selectAll('path')
        .data(() => stack(<any>data.values))
        .enter()
        .append('path')
        .attr('d', d => area(d))
        .attr('fill', d => areaFill(d.key));

      svg.append('g')
        .attr('class', 'y axis ' + absOrRel)
        .call(chartYAxis);

      // x-axis and text are identical for abs and rel data, so only add them once
      if (absOrRel === 'abs') {
        svg.append('g')
          .attr('class', 'x axis')
          .attr('transform', `translate(0, ${chartHeight})`)
          .call(chartXAxis);
        svg.append('text')
          .attr('x', 6)
          .attr('y', 8)
          .attr('class', 'chart-label')
          .text(data.key === 'Alle operatieve verrichtingen BIG TOTAL' ? 'Alle operatieve verrichtingen' : data.key);
      }

      svg.append('rect')
        .attr('class', absOrRel)
        .attr('x', 0)
        .attr('y', 0)
        .attr('width', chartX(new Date(+years[years.length - 1], 0, 1)))
        .attr('height', chartY(0))
        .attr('opacity', '0.0001')
        .on('click', () => showTable(data, absOrRel));
    }

    // Create a container for each operation
    chartsAbs.forEach(operation => {

      let viewBoxHeight = height;
      let viewBoxWidth = width;
      let viewBoxMargin = margin;
      let classed = 'regular-chart';
      let container = chartContainer;

      switch (operation.key) {
        case 'Alle operatieve verrichtingen BIG TOTAL':
          viewBoxHeight = heightTotal;
          viewBoxWidth = widthTotal;
          viewBoxMargin = marginTotal
          classed = 'total-chart';
          container = bigTotalChartContainer;
          break;
        case 'Staaroperatie':
          viewBoxHeight = heightLarge;
          classed = 'large-chart';
          break;
      }

      container.append('div')
        .attr('id', operation.key.toLowerCase().replace(/[\s/,\.]/g, ''))
        .attr('class', 'chart ' + classed)
        .append('svg')
        .attr('viewBox', `0 0 ${viewBoxWidth + viewBoxMargin.left + viewBoxMargin.right} ${viewBoxHeight + viewBoxMargin.top + viewBoxMargin.bottom}`)
        .append('g')
        .attr('transform', `translate(${viewBoxMargin.left}, ${viewBoxMargin.top})`);
    });

    // Create the charts for each operation
    chartsAbs.forEach(operation => chart(operation, 'abs'));
    chartsRel.forEach(operation => chart(operation, 'rel'));

    // On init hide the relative data
    d3.selectAll('.rel').style('visibility', 'hidden');
  });

  /*  The two functions below are for wrangling the data to the form the stack function needs. The form we need is:
  var data = [
    {month: new Date(2015, 0, 1), apples: 3840, bananas: 1920, cherries: 960, dates: 400},
    {month: new Date(2015, 1, 1), apples: 1600, bananas: 1440, cherries: 960, dates: 400},
    ...
  ];
  See https://github.com/d3/d3-shape/blob/master/README.md#stacks
  So we need data like:
  [
    {year: "1995", 'dagopname aantal&enkel': 367908, 'klinisch aantal&enkel': 678796},
    {year: "1996", 'dagopname aantal&enkel': 389024, 'klinisch aantal&enkel': 666644},
    ...      
  ]
  Or: 
  [
    {year: "1995", 'dagopname aantal&laparoscopisch': 0, 'dagopname aantal&overig': 0, 'klinisch aantal&laparoscopisch': 40, 'klinisch aantal&overig': 10599},    
    {year: "1996", 'dagopname aantal&laparoscopisch': 0, 'dagopname aantal&overig': 0, 'klinisch aantal&laparoscopisch': 37, 'klinisch aantal&overig': 11120},
    ...      ]
  And we need such an array for every operation, and for both absolute data and relative data.
  */

  function byYear(operationValuesObj: IStringStringMap, year: string) {
    const label = operationValuesObj.aantal_of_per10000 + '&' + operationValuesObj.wijze;
    const value: number = +operationValuesObj[year];
    return { label: label, value: value };
  }

  function operationWithValuesByYear(operation: { key: string, values: Array<IStringStringMap> }, years: string[]) {

    const operationValues = operation.values;
    const ArrayOfYearValuesObjects = years.map(year => {
      const yearValuesObject: { [key: string]: number | string } = {};
      yearValuesObject.year = year;
      operationValues.forEach(operationValuesObj => yearValuesObject[byYear(operationValuesObj, year).label] = +byYear(operationValuesObj, year).value);
      // Format of yearValuesObject: {year: "1996", klinisch aantal&enkel: 10128, dagopname aantal&enkel: 8562}.
      // It has a year key-value pair and can have two data key-values pairs – like this one – or four data key-value pairs. 
      return yearValuesObject;
    });
    return ({
      key: operation.key,
      values: ArrayOfYearValuesObjects
    })
  }

  function areaFill(key: string) {
    const firstPart = key.split(' ')[0];
    const lastPart = key.split('&')[1];

    if (firstPart === 'dagopname') {
      if (lastPart === 'enkel' || lastPart === 'overig') {
        return palette[0].color;
      } else {
        return palette[2].color;
      }
    } else { // firstPart === 'klinisch'
      if (lastPart === 'enkel' || lastPart === 'overig' || lastPart === 'open') {
        return palette[1].color;
      } else {
        return palette[3].color;
      }
    }
  }

  function showTable(data: { key: string, values: { [key: string]: number | string }[] }, absOrRel: string) {

    const modal = viz.append('div')
      .attr('class', 'w3-modal')
      .style('display', 'block');

    const modalContent = modal.append('div')
      .attr('class', 'w3-modal-content');

    const header = modalContent.append('div')
      .attr('class', 'w3-container modal-header');

    header.append('h5')
      .text(data.key === 'Alle operatieve verrichtingen BIG TOTAL' ? 'Alle operatieve verrichtingen' : data.key);

    header.append('p')
      .text(absOrRel === 'abs' ? '(totaal aantal)' : '(per 10.000 inwoners)');

    header.append('span')
      .attr('class', 'w3-button w3-display-topright')
      .on('click', () => modal.style('display', 'none'))
      .html('&times;');

    const table = modalContent.append('div')
      .attr('class', 'w3-container')
      .append('table')
      .attr('class', 'w3-table-all w3-small w3-margin-top w3-margin-bottom');

    const rowNames = d3.keys(data.values[0])
    const rowLabels = rowNames.map(row =>
      row.replace(/aantal&|per 10000 inw&/, '')
        .replace(/year|enkel/, '')
        .replace('laparoscopisch', 'laparo&shy;scopisch')
        .replace('transurethraal', 'trans&shy;urethraal')
    );

    const thRow = table.append('tr');

    rowLabels.forEach(label => {
      thRow.append('th')
        .html(label);
    });

    table.selectAll('tr')
      .data(data.values)
      .enter()
      .append('tr')
      .selectAll('td')
      .data(d => d3.values(d))
      .enter()
      .append('td')
      .text((d, i) => i > 0 ? format(+d) : d); // Don't format the first row with the year
  }

})();